import {useRef} from 'react';
import classes from './NewProductsForm.module.css';
import Card from '../ui/Card';
import Axios from 'axios';


function UpdateEmployeeForm(props){
    const nameInputRef = useRef();
    const sectionInputRef = useRef();

    function submitHandler(event){
        event.preventDefault();
        const enteredName = nameInputRef.current.value;
        const enteredSection = sectionInputRef.current.value;

        const productData ={
            id:props.id.id,
            name: enteredName,
            section: enteredSection
        }
        const ProductData=JSON.stringify(productData);
        Axios({
            method: 'put',
            url: 'https://reactappdevsoc.herokuapp.com/emp',
            headers: { 'Content-Type' : 'application/json'},
            data: ProductData
          }).then(() => window.location.reload());
    }

    return(
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor="name">Employee's Name </label>
                    <input type="text" required id="name" ref={nameInputRef} placeholder={props.id.name}></input>
                </div>
                <div className={classes.control}>
                    <label htmlFor="section">Employee's Section</label>
                    <input type="text" required id="section" ref={sectionInputRef} placeholder={props.id.section}/>
                </div>
                <div className={classes.actions}>
                    <button>Update Employee</button>
                </div>
            </form>
        </Card>
    );
}

export default UpdateEmployeeForm;