import EmployeeList from '../components/elements/EmployeeList';
import classes from './btn.module.css';
import {useState , useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import Axios from 'axios';

function EmployeePage() {
    const history = useHistory();
    const [isLoading,setIsLoading] = useState(true);
    const [loadedEmployees,setLoadedEmployees]=useState([]);

    useEffect(()=>{
    setIsLoading(true);
    Axios.get("https://reactappdevsoc.herokuapp.com/emp").then((response) =>{
            return response.data;
        }).then(data =>{
            const products = [];
            for(const key in data){
                const product={
                    id:key,
                    ...data[key]
                };
                products.push(product);
            }
            const products1=[];
            for(const i in products[0]){
                const product1 = products[0][i]
                products1.push(product1);
            }
            products1.pop();
            setIsLoading(false);
            setLoadedEmployees(products1);
            console.log(products1);
        })
    },[]);

    if(isLoading){
        return(
            <section><h1>Loading....</h1></section>
        );
    }

    function back(){
        history.replace('/addemployee')
    }

    return( 
        <section>
            <button className={classes.btn} onClick={back}>Add Employee +</button>
            <br/><br/>
            <h1 className={classes.h1}>Employee-</h1>
            <EmployeeList  employee={loadedEmployees}/>
        </section>
    );
}

export default EmployeePage;