import classes from './ProductList.module.css';
import SectionsItem from './SectionItem';
import Backdrop from './Backdrop';
import {useState} from 'react';
import UpdateSectionForm from './UpdateSectionForm';
import SectionsView from './SectionsView';

function SectionsList(props){
    const [modalIsOpen,setModalIsOpen] = useState(false);
    const [modalIsOpen1,setModalIsOpen1] = useState(false);
    const [loadedId,setLoadedId]=useState([]);
    const [Sect,setSect]=useState([]);

    function componentDidMount() {
        window.scrollTo(0, 0)
      }

    function Update(data,name,desc){ 
        const products={id:data,name:name,desc:desc}
        console.log(products);
        setLoadedId(products);
        setModalIsOpen(true);
    }
    function closeModalHandler(){
        setModalIsOpen(false);
        setModalIsOpen1(false);
    }

    function data1(data,name){ 
        const products={id:data,name:name};
        setSect(products);
        setSect(products);
        console.log(Sect);
        setModalIsOpen1(true);
        componentDidMount();
    }
    function back(){
        setModalIsOpen(false);
        setModalIsOpen1(false);
    }

    return(
        <div>
            {modalIsOpen1 ? <SectionsView data1={Sect} back={back}/>:null}
            {modalIsOpen1 ? <Backdrop onClick={closeModalHandler}/>:null}
            {modalIsOpen ? <UpdateSectionForm id={loadedId}/>:null}
            {modalIsOpen ? <Backdrop onClick={closeModalHandler}/>:null}
        <ul className={classes.list}>
            {props.section.map(section =><SectionsItem
                key={section.id} id={section.id} 
                name={section.name} 
                disc={section.desc}
                Id={section.id}
                update={Update}
                data1={data1}
                />)}
        </ul>
        </div>
    );
}

export default SectionsList;