import {useRef} from 'react';
import classes from './NewProductsForm.module.css';
import Card from '../ui/Card';


function NewProductForm(props){
    const nameInputRef = useRef();
    const costInputRef = useRef();
    const sectionInputRef = useRef();

    function submitHandler(event){
        event.preventDefault();
        const enteredName = nameInputRef.current.value;
        const enteredCost = costInputRef.current.value;
        const enteredSection = sectionInputRef.current.value;

        const productData ={
            name: enteredName,
            cost: enteredCost,
            sect: enteredSection
        }
        console.log(productData);
        props.onAddProducts(productData);
    }

    return(
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor="name">Product's Name</label>
                    <input type="text" required id="name" ref={nameInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor="cost">Product's Cost</label>
                    <input type="text" required id="cost" ref={costInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor="section">Product's Section</label>
                    <input type="text" required id="section" ref={sectionInputRef}/>
                </div>
                <div className={classes.actions}>
                    <button>Add Product</button>
                </div>
            </form>
        </Card>
    );
}

export default NewProductForm;