import {useHistory} from 'react-router-dom';
import classes from './btn.module.css';
import NewSectionForm from '../components/elements/NewSectionForm';
import Axios from 'axios';

function AddSectionPage() {

    const history = useHistory();
    function addMeetupHandler(meetupData){
        
        const Meetup=JSON.stringify(meetupData);

        Axios({
            method: 'post',
            url: 'https://reactappdevsoc.herokuapp.com/sec',
            data: meetupData
          }).then(() => {history.replace('/section')});
          console.log(Meetup);

    }

    return(
        <section>
            <h1 className={classes.h1}> 
                Add Sections
            </h1>
            <NewSectionForm onAddSections={addMeetupHandler}/>
        </section>
    );
}

export default AddSectionPage;