import {useHistory} from 'react-router-dom';
import classes from './btn.module.css';
import NewProductsForm from '../components/elements/NewProductsForm';
import Axios from 'axios';

function AddProductsPage() {

    const history = useHistory();
    function addMeetupHandler(meetupData){
        
        const Meetup=JSON.stringify(meetupData);

        Axios({
            method: 'post',
            url: 'https://reactappdevsoc.herokuapp.com/pr',
            data: meetupData
          }).then(() => {history.replace('/products')});
          console.log(Meetup);

    }

    return(
        <section>
            <h1 className={classes.h1}> 
                Add Products
            </h1>
            <NewProductsForm onAddProducts={addMeetupHandler}/>
        </section>
    );
}

export default AddProductsPage;