import ProductList from '../components/elements/ProductList';
import classes from './btn.module.css';
import {useState , useEffect} from 'react';
import {useHistory} from 'react-router-dom';
import Axios from 'axios';


function ProductPage() {
    const history = useHistory();
    const [isLoading,setIsLoading] = useState(true);
    const [loadedProducts,setLoadedProducts]=useState([]);
    const [searchTerm,setSearchTerm]=useState('');
    useEffect(()=>{
    setIsLoading(true);

    Axios.get("https://reactappdevsoc.herokuapp.com/pr").then((response) =>{
            return response.data;
        }).then(data =>{
            const products = [];
            for(const key in data){
                const product={
                    id:key,
                    ...data[key]
                };
                products.push(product);
            }
            const products1=[];
            for(const i in products[0]){
                const product1 = products[0][i]
                products1.push(product1);
            }
            products1.pop();
            setIsLoading(false);
            setLoadedProducts(products1);
            console.log(products1);
        })
    },[]);

    if(isLoading){
        return(
            <section><h1>Loading....</h1></section>
        );
    }

    function back(){
        history.replace('/addproducts')
    }

    return( 
        <section>
            <div className={classes.container}>
            <button className={classes.btn} onClick={back}>Add Product +</button>
            <input className={classes.inp} type="text" required id="desc" onChange={event =>{setSearchTerm(event.target.value)}}  placeholder="Search"/>
            </div>
            <br/><br/>
            <h1 className={classes.h1}>Products-</h1>
            <ProductList  products={loadedProducts} search={searchTerm}/>
        </section>
    );
}

export default ProductPage;