import classes from './ProductList.module.css';
import EmployeeItem from './EmployeeItem';
import Backdrop from './Backdrop';
import {useState} from 'react';
import UpdateEmployeeForm from './UpdateEmployeeForm';

function EmployeeList(props){
    const [modalIsOpen,setModalIsOpen] = useState(false);
    const [loadedId,setLoadedId]=useState([]);

    function Update(data,name,section){ 
        const products={id:data,name:name,section:section}
        console.log(products);
        setLoadedId(products);
        setModalIsOpen(true);
    }
    function closeModalHandler(){
        setModalIsOpen(false);
    }

    return(
        <div>
            {modalIsOpen ? <UpdateEmployeeForm id={loadedId}/>:null}
            {modalIsOpen ? <Backdrop onClick={closeModalHandler}/>:null}
        <ul className={classes.list}>
            {props.employee.map(employee =><EmployeeItem
                key={employee.id} id={employee.id} 
                name={employee.name} 
                section={employee.section}
                Id={employee.id}
                update={Update}
                />)}
        </ul>
        </div>
    );
}

export default EmployeeList;