import classes from './ProductItem.module.css'
import Card from '../ui/Card';
import axios from 'axios';



function ProductItem(props){
    const id=props.Id;
    const Id=JSON.stringify({"id":id});

    function componentDidMount() {
        window.scrollTo(0, 0)
      }

    function del(){
        
        console.log(Id);

        axios({
            method: 'delete',
            url: 'https://reactappdevsoc.herokuapp.com/pr',
            headers: {'Content-Type':'application/json'},
            data: Id
          }).then(() => window.location.reload());
    }
    function one(){
        props.update(props.id,props.name,props.cost,props.section);
        componentDidMount();
    }
   
    return(
        <div>
        <Card>
        <li className={classes.item}> 
            <div className={classes.content}>
                <h3>Name - {props.name}</h3>
                <h3>Cost - {props.cost}</h3>
                <h3>Section - {props.section}</h3>
            </div>
            <div className={classes.actions}>
                <button onClick={one}>Update</button>
                <button onClick={del}>Delete</button>
            </div>
        </li>
        </Card>
        </div>
    );
}

export default ProductItem;