import Axios from 'axios';
import {useState , useEffect} from 'react';
import classes from './ProductItem.module.css'
import SectionViewItem from './SectionViewItem';

function SectionsView(props){
    const [loadedProducts,setLoadedProducts]=useState([]);

    useEffect(()=>{
        Axios.get("https://reactappdevsoc.herokuapp.com/pr").then((response) =>{
                return response.data;
            }).then(data =>{
                const products = [];
                for(const key in data){
                    const product={
                        id:key,
                        ...data[key]
                    };
                    products.push(product);
                }
                const products1=[];
                for(const i in products[0]){
                    const product1 = products[0][i]
                    products1.push(product1);
                }
                products1.pop();
                setLoadedProducts(products1);
                console.log(loadedProducts);
            })
        },[loadedProducts]);

    
    return(
        <div>
        <div className={classes.contsectop}>
        <div className={classes.section}>Products Under Section - {props.data1.name}</div>
        <button className={classes.btnxyz} onClick={props.back} > Back </button>
        </div>
        
        <ul className={classes.list}>
            {loadedProducts.filter((val) =>{
             if(String(val.sect)===(String(props.data1.name))){
                console.log(String((val.sect)));
                return val 
             }
             else{
                 return null
             }    
            }).map(product =><SectionViewItem
                key={product.id} id={product.id} 
                name={product.name} 
                cost={product.cost}
                section={product.sect}
                />)}
        </ul>
        </div>
    );

}
export default SectionsView;