import {useRef} from 'react';
import classes from './NewProductsForm.module.css';
import Card from '../ui/Card';


function NewEmployeeForm(props){
    const empnameInputRef = useRef();
    const empsectionInputRef = useRef();

    function submitHandler(event){
        event.preventDefault();
        const enteredEmpname = empnameInputRef.current.value;
        const enteredEmpsection = empsectionInputRef.current.value;

        const employeeData ={
            name: enteredEmpname,
            section: enteredEmpsection
        }
        console.log(employeeData);
        props.onAddEmployees(employeeData);
    }

    return(
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor="empname">Employee's Name</label>
                    <input type="text" required id="empname" ref={empnameInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor="empsection">Employee's Section</label>
                    <input type="text" required id="empsection" ref={empsectionInputRef}/>
                </div>
                <div className={classes.actions}>
                    <button>Add Employee</button>
                </div>
            </form>
        </Card>
    );
}

export default NewEmployeeForm;