import classes from './ProductList.module.css';
import ProductItem from './productItem';
import Backdrop from './Backdrop';
import {useState} from 'react';
import UpdateProductsForm from './UpdateProductsForm';


function ProductList(props){
    const [modalIsOpen,setModalIsOpen] = useState(false);
    const [loadedId,setLoadedId]=useState([]);

    function Update(data,name,cost,section){ 
        const products={id:data,name:name,cost:cost,section:section}
        console.log(products);
        setLoadedId(products);
        setModalIsOpen(true);
    }
    function closeModalHandler(){
        setModalIsOpen(false);
    }
    console.log(props.search.toLowerCase())
    //console.log(props.products[0].name)
    
    return(
        <div>
            {modalIsOpen ? <UpdateProductsForm id={loadedId}/>:null}
            {modalIsOpen ? <Backdrop onClick={closeModalHandler}/>:null}     
        <ul className={classes.list}>
            {props.products.filter((val) =>{
             if(props.search === ""){
                return val 
             }
             else if(val.name.toLowerCase().includes(props.search.toLowerCase())){
                 return val
             }
             else{
                 return null
             }    
            }).map(product =><ProductItem
                key={product.id} id={product.id} 
                name={product.name} 
                cost={product.cost}
                section={product.sect}
                Id={product.id}
                update={Update}
                />)}
        </ul>
        </div>
    );
}

export default ProductList;