import {useRef} from 'react';
import classes from './NewProductsForm.module.css';
import Card from '../ui/Card';


function NewSectionForm(props){
    const nameInputRef = useRef();
    const descInputRef = useRef();

    function submitHandler(event){
        event.preventDefault();
        const enteredName = nameInputRef.current.value;
        const enteredDiscription = descInputRef.current.value;

        const SectionsData ={
            name: enteredName,
            desc: enteredDiscription
        }
        console.log(SectionsData);
        props.onAddSections(SectionsData);
    }

    return(
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor="name">Sections's Name</label>
                    <input type="text" required id="name" ref={nameInputRef}/>
                </div>
                <div className={classes.control}>
                    <label htmlFor="desc">Section's Description</label>
                    <input type="text" required id="desc" ref={descInputRef}/>
                </div>
                <div className={classes.actions}>
                    <button>Add Section</button>
                </div>
            </form>
        </Card>
    );
}

export default NewSectionForm;