import {useRef} from 'react';
import classes from './NewProductsForm.module.css';
import Card from '../ui/Card';
import Axios from 'axios';


function UpdateSectionForm(props){
    const nameInputRef = useRef();
    const descInputRef = useRef();

    function submitHandler(event){
        event.preventDefault();
        const enteredName = nameInputRef.current.value;
        const enteredDesc = descInputRef.current.value;

        const productData ={
            id:props.id.id,
            name: enteredName,
            desc: enteredDesc
        }
        const ProductData=JSON.stringify(productData);
        Axios({
            method: 'put',
            url: 'https://reactappdevsoc.herokuapp.com/sec',
            headers: { 'Content-Type' : 'application/json'},
            data: ProductData
          }).then(() => window.location.reload());
    }

    return(
        <Card>
            <form className={classes.form} onSubmit={submitHandler}>
                <div className={classes.control}>
                    <label htmlFor="name">Section's Name </label>
                    <input type="text" required id="name" ref={nameInputRef} placeholder={props.id.name}></input>
                </div>
                <div className={classes.control}>
                    <label htmlFor="desc">Section's Discription</label>
                    <input type="text" required id="desc" ref={descInputRef} placeholder={props.id.desc}/>
                </div>
                <div className={classes.actions}>
                    <button>Update Section</button>
                </div>
            </form>
        </Card>
    );
}

export default UpdateSectionForm;